.hamburger {
    position: fixed;
    top: 6.5rem;
    left: 1rem;
    width: 2rem;
    height: 1.5rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    background: transparent;
    border: none;
    cursor: pointer;
    z-index: 1100;
    padding: 0;
}

.hamburger span {
    display: block;
    height: 0.25rem;
    background-color: white;
    border-radius: 0.125rem;
}


.sideMenu {
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    background-color: rgb(237,154,193);
    overflow: hidden;
    padding-top: 4rem;
    box-sizing: border-box;
    transition: height 0.3s ease;
    z-index: 1101;
    display: none;
    flex-direction: column;
    align-items: center;
}

.sideMenu.open {
    height: 100%;
    width: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sideMenu.open .closeButton {
    position: fixed;
    top: 1rem;
    right: 1rem;
    font-size: 28px;
    background: transparent;
    border: none;
    color: black;
    cursor: pointer;
    z-index: 1110;
}

.appLinksMobile {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: column;
    gap: 1rem;
    list-style: none;
    padding: 0 0;
}

.appLinksMobile li {
    margin: 0.5rem 0.5rem;
    cursor: pointer;
    padding: 2rem 3rem;
    border-radius: 5px;
    font-size: 1.5rem;
    user-select: none;
    transition: background-color 0.3s ease;
    min-width: 100px;
    text-align: start;
}

.appLinksMobile li a {
    color: black;
    font-weight: bold;
    text-decoration: none;
}


.appLinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    margin: 0 auto;
    padding: 0 0;
    border: 1px;
    border-radius: 4px;
    background-color: transparent;
    box-sizing: border-box;
}


.appLinks li {
    margin: 0.5rem 0.5rem;
    cursor: pointer;
    padding: 0.5rem 0.5rem;
    border-radius: 5px;
    font-size: 1.1rem;
    user-select: none;
    transition: background-color 0.3s ease;
    min-width: 100px;
    text-align: center;
}

.appLinks li a.name {
    color: white;
    font-weight: normal;
    text-decoration: none;
}

.appLinks li:hover {
    background-color: #e0e0e0; /* выделение при наведении */
}

@media (max-width: 768px) {
    .hamburger {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .sideMenu {
        display: block;
    }
    .appLinks {
        display: none;
    }
}
