.notification {
    margin: 0 auto;
    padding: 0.75rem 1.25rem;
    max-width: 43.75rem;
    background-color: white;
    color: black;
    border-radius: 0.375rem;
    box-shadow: 0 0.125rem 0.5rem rgba(0,0,0,0.1);
    text-align: center;
    cursor: pointer;
    user-select: none;
    font: 300 1rem / 0.625rem 'Fredoka One', sans-serif;
}