.cartContainer {
    max-width: 100%;
    margin: 1rem auto;
    padding: 1rem;
    border-radius: 0.5rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
}

.cartContainer h2 {
    margin-bottom: 0.125rem;
    font-size: 3rem;
    border-bottom: 0.1rem solid rgb(237,154,193);
    padding-bottom: 0.5rem;
    color: rgb(237,154,193);
}

.cartHeader, .cartItem {
    display: grid;
    grid-template-columns: 3fr 1.5fr 1.5fr; /* название занимает больше места */
    gap: 1rem;
    align-items: center;
    padding: 0.8rem 0;
    border-bottom: 0.1rem solid #eee;
}

.cartHeader {
    font-weight: 700;
    border-bottom: 0.2rem solid rgb(237,154,193);
}

.itemName {
    font-size: 1.5rem;
    word-wrap: break-word;
}

.itemQuantity {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    font-size: 1.5rem;
    color: white;
}

.qtyBtn {
    background-color: rgb(237,154,193);
    border: none;
    color: white;
    padding: 0.25rem 1rem;
    font-size: 1.3rem;
    border-radius: 0.25rem;
    cursor: pointer;
    user-select: none;
    transition: background-color 0.3s ease;
}

.qtyBtn:hover {
    background-color: rgb(237,154,193);
}

.qtyBtn:active {
    transform: scale(0.95);
}

.qtyNumber {
    min-width: 1.8rem;
    text-align: center;
    font-weight: 600;
    font-size: 1.1rem;
}

.itemPrice {
    font-size: 1.5rem;
    color: white;
    text-align: center;
    white-space: nowrap;
}

.cartTotal {
    text-align: right;
    margin-top: 1.5rem;
    font-size: 1.4rem;
    font-weight: 700;
    color: white;
}

.emptyCartMessage {
    font-style: italic;
    color: white;
    padding: 1.4rem 0;
    text-align: center;
}
