.cardsContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
}

.dishCard {
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0.625rem 0.125rem 0.5rem rgba(0,0,0,0.1);
    width: 17rem;          /* фиксированная ширина */
    height: auto;          /* высота подстраивается под содержимое */
    font-family: Arial, sans-serif;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
}

/* остальное без изменений */
.dishCard img {
    width: 100%;
    height: 17rem;
    object-fit: cover;
    display: block;
    flex-shrink: 0;
}

.dishCardInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 1rem;
    padding: 0.5rem;
    box-sizing: border-box;
}

.dishCardInfo h3 {
    font-size: 2rem;
    margin: 0.5rem 0;
    word-wrap: break-word;
    overflow-wrap: break-word;
    text-align: center;
    width: 100%;
}

.price, .buy {
    display: block;
    padding: 0.5rem;
    box-sizing: border-box;
    width: 100%;
    text-align: center;
}

.price {
    font-size: 1.2rem;
    font-weight: normal;
}
.buy {
    background-color: rgb(237,154,193);
    border: 1px solid;
    border-radius: 0.625rem;
    font-size: 1.5rem;
    font-weight: normal;
    color: white;
}
