.dish {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.notification {
    height: 1rem;
    width: auto;
}

.wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 2.5rem 0;
    margin: 1rem auto;
}

.wrapper .container {
    display: flex;
    overflow: hidden;
    gap: 6.25rem
}


.imageWrapper {
    width: 25rem;
}

.image {
    width: 25rem;
    height: auto;
    object-fit: cover;
    border: 0.25rem solid white;
    border-radius: 0.5rem;
    box-sizing: border-box;
}

.info {
    flex: 2;
    padding: 1.25rem;
    display: flex;
    flex-direction: column;
}

.section {
    font-size: 1.3rem;
    white-space: nowrap;
    color: hsl(0, 0%, 50%);
    font-weight: bold;
    margin-bottom: 1rem;
}

.title {
    color: white;
    font: 900 3rem / 3.4rem 'Fredoka One', sans-serif;
    margin: .67em 0;
}


.description {
    font-size: large;
    color: white;
    flex-grow: 0;
    margin-bottom: 1.5rem;
}

.button {
    margin-top: auto;
    padding: 0.75rem 1.25rem;
    border: none;
    border-radius: 0.375rem;
    background-color: #007bff;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button:hover {
    background-color: #0056b3;
}
