.app {
    width: 100%;
    height: 16rem;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color:  rgb(2,53,36);

    position: fixed;
    top: 0;
    left: 0;

    z-index: 100;

    overflow: hidden;
}

.app .topContact {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-bottom: 2rem;
    font-size: 0.8rem;
    color: white;
    text-shadow: 0 0 4px rgba(0,0,0,0.7);
    box-sizing: border-box;
    user-select: text;
}

.maps, .telephoneNumber {
    cursor: pointer;
}

.telephoneNumber, .orderTime, .maps {
    margin: 1rem 0.5rem;
    padding: 0.2rem 0.2rem;
    color: white;
    font-weight: normal;
    min-width: 50px;
}


.topRow {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 75rem;
    position: relative;
}

.topRow .appLogo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0 auto;
}

.topRow .appLogo img {
    width: 9rem;
    display: block;
    margin: 0 auto;
}

.topRow button.bucket {
    position: absolute;
    right: 0;
    display: block;
    background-color: transparent;
    border: none;
}

.topRow button.bucket img {
    width: 50px;
    cursor: pointer;
}

.appLinks {
    width: 100%;
    max-width: 9rem;
    display: flex;
    justify-content: center; /* меню по центру */
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;
    gap: 20px;
}

.appLinks li {
    cursor: pointer;
}
